import React from 'react'
import popularDestination from '../assets/popular-destination.png';
import popularDestination2 from '../assets/popular-destination-2.png'; 
import popularDestination3 from '../assets/popular-destination-3.png';
import popularDestination4 from '../assets/popular-destination-4.png';
import popularDestination5 from '../assets/popular-destination-5.png';
import popularDestination6 from '../assets/popular-destination-6.png';
import DestinationCard from './props/DestinationCard';

export default function PopularDestination() {
    return (
        <div>
            <div className="popular-destination py-5">
                <div className="container p-5">
                    <div className="title">
                        <h1>Popular Destination</h1>
                        <p>Suffered alteration in some form, by injected humour or good day randomised booth anim 8-bit hella wolf moon beard words.</p>
                    </div>

                    <div className="row">
                        <DestinationCard imgsrc={popularDestination} country="France" places="07"/>
                        <DestinationCard imgsrc={popularDestination2} country="Brazil" places="03"/>
                        <DestinationCard imgsrc={popularDestination3} country="America"  places="10"/>
                        <DestinationCard imgsrc={popularDestination4} country="Nepal" places="02"/>
                        <DestinationCard imgsrc={popularDestination5} country="Maldives" places="02"/>
                        <DestinationCard imgsrc={popularDestination6} country="Indonesia" places="05"/>
                    </div>

                </div>
            </div>
        </div>
    )
}
