import React from 'react'
import VideoArea from '.././components/Videoarea';
import TravelVariation from '.././components/TravelVariation';
import RecentTrips from '.././components/RecentTrips';
import OurStory from '.././components/OurStory';
import Hero from '../components/props/Hero';
import AboutBanner from '../assets/bradcam3.png';

export default function About() {
    return (
        <div>
            <Hero img={AboutBanner} heading="About Us" paragraph="Pixel Perfect Design With Awesome Contents"/>
            <OurStory/>
            <VideoArea/>
            <TravelVariation/>
            <RecentTrips/>
        </div>
    )
}
