import React, { Component } from 'react';
import banner from '../assets/banner.png';
import banner2 from '../assets/banner 2.png';
import banner3 from '../assets/banner 3.png';

export default class Banner extends Component {
    render() {
        return (
            <div>
                <div id="carouselExampleCaptions" className="carousel slide carousel-fade" data-bs-ride="carousel">

                    <div className="carousel-inner">
                        <div className="carousel-item active">
                            <img src={banner} className="d-block w-100" alt="..." />
                            <div className="carousel-caption d-md-block">
                                <h5>Indonasia</h5>
                                <p>Pixel perfect design with awesome contents</p>
                                <div className="btn">Explore Now</div>
                            </div>
                        </div>
                        <div className="carousel-item">
                            <img src={banner2} className="d-block w-100" alt="..." />
                            <div className="carousel-caption d-md-block">
                                <h5>Australia</h5>
                                <p>Pixel perfect design with awesome contents</p>
                                <div className="btn">Explore Now</div>
                            </div>
                        </div>
                        <div className="carousel-item">
                            <img src={banner3} className="d-block w-100" alt="..." />
                            <div className="carousel-caption d-md-block">
                                <h5>Switzerland</h5>
                                <p>Pixel perfect design with awesome contents</p>
                                <div className="btn">Explore Now</div>
                            </div>
                        </div>
                    </div>
                    <button className="carousel-control-prev" type="button" data-bs-target="#carouselExampleCaptions" data-bs-slide="prev">
                        <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                        <span className="visually-hidden">Previous</span>
                    </button>
                    <button className="carousel-control-next" type="button" data-bs-target="#carouselExampleCaptions" data-bs-slide="next">
                        <span className="carousel-control-next-icon" aria-hidden="true"></span>
                        <span className="visually-hidden">Next</span>
                    </button>
                </div>

                <div className="search-con py-5">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-3 col-12">
                                <p>Where you want to go?</p>
                            </div>
                            <div className="col-lg-9 col-12">
                                <form className="d-flex justify-content-between">
                                    <input type="text" name="" placeholder="Where to go?" />
                                    <input type="date" name="" placeholder="Date"/>
                                    <select className="form-select form-select-lg mb-3" aria-label=".form-select-lg example" value="0" readOnly>
                                        <option value="0">Travel Type</option>
                                        <option value="1">Some Option</option>
                                        <option value="2">More Option</option>
                                    </select>
                                    <div className="btn btn-primary">Search</div>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

        )

    }
}
