import React from 'react';
import recentTrips from '../assets/recent-trips.png';
import recentTrips2 from '../assets/recent-trips-2.png';
import recentTrips3 from '../assets/recent-trips-3.png';
import RecentTipsCard from './props/RecentTipsCard';

export default function RecentTrips() {
    return (
        <div>
            <div className="recent-trips py-5">
                <div className="container p-5">
                    <div className="title">
                        <h1>Recent Tips</h1>
                    </div>
                    <div className="row">
                       

                    <RecentTipsCard imgsrc={recentTrips}/>
                    <RecentTipsCard imgsrc={recentTrips2}/>
                    <RecentTipsCard imgsrc={recentTrips3}/>
                           
                               
                            
                        
                    </div>
                </div>

            </div>
        </div>
    )
}
