import React from 'react'

export default function RecentTipsCard(props) {
    return (
        <>
            <div className="col-lg-4 col-md-6">
                            <div className="card">
                                <div className="img-wrapper">
                                    <img src={props.imgsrc} alt="" srcSet="" className="img-fluid" />
                                </div>
                                <div className="card-details">
                                    <div className="date">
                                        Oct 12, 2019
                                    </div>
                                    <h1>
                                        Journeys Are Best Measured In New Friends
                                    </h1>
                                </div>
                            </div>

                        </div> 
        </>
    )
}
