import React from 'react'

export default function Newsletter() {
    return (
        <div>
            <div className="newsletter-container py-5">
                <div className="container">
                    <div className="row px-5 align-items-center">
                        <div className="col-lg-5 col-md-12">
                            <h1>Subscribe Our Newsletter</h1>
                            <p>Subscribe newsletter to get offers and about new places to discover.</p>
                        </div>
                        <div className="col-lg-7 col-md-12">
                            <div className="row">
                                <div className="col-lg-8 col-md-6">
                                    <input type="text" name="" id="" className="form-control" placeholder="Your Mail"/>
                                </div>
                                <div className="col-lg-4 col-md-6">
                                    <div className="btn btn-primary">Subscribe</div></div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
