import React from 'react';
import popularPlaces from '../assets/popular-places.png';
import popularPlaces2 from '../assets/popular-places-2.png';
import popularPlaces3 from '../assets/popular-places-3.png';
import popularPlaces4 from '../assets/popular-places-4.png';
import popularPlaces5 from '../assets/popular-places-5.png';
import popularPlaces6 from '../assets/popular-places-6.png';
import PopularPlacesCard from './props/PopularPlacesCard';
export default function PopularPlaces() {
    return (
        <div>
            <div className="popular-places-area py-5">
                <div className="container p-5">
                    <div className="title">
                        <h1>Popular Places</h1>
                        <p>Suffered alteration in some form, by injected humour or good day randomised booth anim 8-bit hella wolf moon beard words.</p>
                    </div>

                    <div className="row ">
                        <PopularPlacesCard imgsrc={popularPlaces} place="California" />
                        <PopularPlacesCard imgsrc={popularPlaces2} place="Korola Megna" />
                        <PopularPlacesCard imgsrc={popularPlaces3} place="London" />
                        <PopularPlacesCard imgsrc={popularPlaces4} place="Miami Beach" />
                        <PopularPlacesCard imgsrc={popularPlaces5} place="California" />
                        <PopularPlacesCard imgsrc={popularPlaces6} place="Saintmartine Iceland" />
                        
                        <div className="col-lg-12 d-none d-md-block"><a href="#" className="btn btn-primary my-5 mx-auto">More Places</a></div>
                    </div>
                </div>
            </div>
        </div>
    )
}
