import React from 'react';
import logo from '../assets/logo.png';
import { Link } from "react-router-dom";
import { useState } from 'react';
import $ from 'jquery';



export default function Header() {
    const [navbar, setNavbar] = useState(false);


    const changeNavbar = () => {
        if (window.scrollY >= 300) {
            setNavbar(true);
        }
        else
            setNavbar(false);
    }

    window.addEventListener('scroll', changeNavbar)
    return (
        <>

            <nav className={navbar ? 'navbar navbar-expand-lg navbar-light p-0 sticky-header' : 'navbar navbar-expand-lg navbar-light p-0'} id="navbar">
                <div className="container-fluid p-0">
                    <Link className="navbar-brand px-5 py-4" to="/"><img src={logo} alt="" srcSet="" /></Link>
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarSupportedContent">


                        <ul className="navbar-nav mb-2 mb-lg-0 px-5">
                            <li className="nav-item">
                                <Link to="/" className="nav-link active" aria-current="page" >Home</Link>
                            </li>
                            <li className="nav-item">
                                <Link to="/about" className="nav-link" >About</Link>
                            </li>
                            <li className="nav-item">
                                <Link className="nav-link" to="#">Destination</Link>
                            </li>
                            <li className="nav-item dropdown">
                                <Link className="nav-link dropdown-toggle" to="#" id="navbarDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                                    Pages
                                </Link>
                                <ul className="dropdown-menu" aria-labelledby="navbarDropdown">
                                    <li><Link className="dropdown-item" to="#">Destination Details</Link></li>
                                    <li><Link className="dropdown-item" to="#">Elements</Link></li>
                                </ul>
                            </li>
                            <li className="nav-item dropdown">
                                <Link className="nav-link dropdown-toggle" to="#" id="navbarDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                                    Blog
                                </Link>
                                <ul className="dropdown-menu" aria-labelledby="navbarDropdown">
                                    <li><Link className="dropdown-item" to="#">Blog</Link></li>
                                    <li><Link className="dropdown-item" to="#">Single-Blog</Link></li>
                                </ul>
                            </li>
                            <li className="nav-item">
                                <Link className="nav-link" to="/contact">Contact</Link>
                            </li>

                        </ul>

                        <div className="d-flex ms-auto nav-info d-none d-lg-flex ">
                            <p> <i className="fas fa-phone"></i> 10(256)-928 256</p>

                            <ul className="social-bar">
                                <li className="social-item"><Link to="#" className="social-link"><i className="fab fa-instagram"></i></Link></li>
                                <li className="social-item"><Link to="#" className="social-link"><i className="fab fa-linkedin-in"></i></Link></li>
                                <li className="social-item"><Link to="#" className="social-link"><i className="fab fa-facebook-f"></i></Link></li>
                                <li className="social-item"><Link to="#" className="social-link"><i className="fab fa-google-plus-g"></i></Link></li>
                            </ul>

                            <button type="button" className="btn btn-primary search" data-bs-toggle="modal" data-bs-target="#exampleModal">
                                <i className="fas fa-search" />
                            </button>


                            {/* <!-- Button trigger modal --> */}

                        </div>
                        {/* <!-- Modal --> */}
                        <div className="modal fade" id="exampleModal" tabIndex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
                            <div className="modal-dialog modal-dialog-centered">
                                <div className="modal-content">

                                    <input type="search" name="" id="" placeholder="Search" /> <button type="button" className="btn btn-primary">Search</button>

                                </div>
                            </div>
                        </div>

                    </div>
                </div>
            </nav>


        </>
    )
}



console.log($(window).scrolltop)

