import React from 'react'

export default function Hero(props) {
    return (
        <div>
            <div className="hero">
                <img src={props.img} alt="" />
                <div className="title">
                    <h1>{props.heading}</h1>
                    <p>{props.paragraph}</p>
                </div>
            </div>
        </div>
    )
}
