import React from 'react'

export default function PopularPlacesCard(props) {
    return (
        <>
            <div className="col-lg-4 col-md-6">
                            <div className="card">
                                <div className="img-wrapper">
                                    <img src={props.imgsrc} alt="" srcSet="" className="img-fluid" />
                                </div>
                                <span>$500</span>
                                <div className="card-details">
                                    <h1>{props.place}</h1>
                                    <p>United State of America</p>
                                    <div className="reviews-date-container d-flex justify-content-between">
                                        <div className="review">
                                            <div className="stars">
                                                <i className="fas fa-star"></i>
                                                <i className="fas fa-star"></i>
                                                <i className="fas fa-star"></i>
                                                <i className="fas fa-star"></i>
                                                <i className="fas fa-star"></i>
                                            </div>
                                            (20 Review)
                                        </div>
                                        <div className="date">

                                            <i className="far fa-clock"></i> 5 Days
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
        </>
    )
}
