import React from 'react'

export default function Getintouch() {
    return (
        <div>
            <div className="getintouch pb-5 px-md-5">
                <div className="container">

                    <h1>Get in Touch</h1>

                    <div className="row">
                        <div className="col-lg-8">
                            <div className="my-3">
                                <textarea className="form-control" id="exampleFormControlTextarea1" rows="7" placeholder="Enter Message"></textarea>
                            </div>

                            <div className="row  ">
                                <div className="col-6 my-3">

                                    <input type="text" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Enter your name" />


                                </div>
                                <div className="col-6 my-3 ">

                                    <input type="email" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Enter your email address" />

                                </div>

                            </div>
                            <div className="col-auto my-3">

                                <input type="text" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Enter Subject" />

                            </div>
                            <div className="col-auto my-5">
                                <button className="btn">SEND</button>
                            </div>
                        </div>
                        <div className="col-lg-4">
                            <div className="d-flex my-2 justify-content-center row ">
                                <div className="col-3 text-end">
                                    <i className="fas fa-home"></i>
                                </div>
                                <div className="text col-9">
                                    <h1>Buttonwood, California.</h1>
                                    <p>Rosemead, CA 91770</p>
                                </div>

                            </div>
                            <div className="d-flex my-2 justify-content-center row">
                                <div className="col-3 text-end">
                                    <i className="fas fa-tablet me-1"></i>
                                </div>

                                <div className="text col-9">
                                    <h1>+1 253 565 2365</h1>
                                    <p>Mon to Fri 9am to 6pm</p>
                                </div>
                            </div>
                            <div className="d-flex my-2 justify-content-center row">
                                <div className="col-3 text-end">
                                    <i className="fas fa-envelope "></i>
                                </div>
                                <div className="text col-9">
                                    <h1>support@colorlib.com</h1>
                                    <p>Send us your query anytime!</p>
                                </div>
                            </div>





                        </div>
                    </div>



                </div>
            </div>
        </div >
    )
}
